<template>
    <div id="new-student">
        <h3>Add New Student</h3>
        <div class="row">
            <form @submit.prevent="saveStudent" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="student_id" required>
                        <label>Student ID Number</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <label>Student Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="date" v-model="birthday" required>
                        <label>Birthday</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="gender" required>
                        <label>Gender</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="grade" required>
                        <label>Grade</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="section" required>
                        <label>Section</label>
                    </div>
                </div>
                <div class="view-options">
                    <button type="submit" class="btn btn-large bg-icon">Submit</button>
                    <router-link to="/s" class="btn btn-large grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'new-student',
    data () {
        return {
            student_id: null,
            name: null,
            grade: null,
            gender: null,
            section: null,
            birthday: null
        }
    },
    methods: {
        saveStudent() {
            db.collection('students').add({
                student_id: this.student_id,
                name: this.name,
                grade: this.grade,
                gender: this.gender,
                section: this.section,
                birthday: this.birthday
            }).then(docRef => {
                this.$router.push('/s')
            })
            //in case anything goes wrong
            .catch(error = console.log(err))
        }
    }
}
</script>


