<template>
    <div id="student-list">
        <ul class="collection with-header emp-top">
            <li class="collection-header opaque-bg">
                <h4 style="margin: 5px;">Student List</h4>
            </li>
            <!-- bind with firebase id -->
            <li v-for="student in students" v-bind:key="student.id" class="collection-item opaque-bg">
                <div class="chip">{{student.grade}}</div>
                {{student.name}}                
                <router-link class="secondary-content view-icon" v-bind:to="{name:'view-student', params: {student_id: student.student_id}}">
                    <i class="fa fa-eye"></i> View
                </router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/s/new" class="btn-floating btn-large bg-icon">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
import db from './firebaseinit'
export default {
    name: 'student-list',
    data () {
        return {
            students: []
        }
    },
    created () {
        db.collection('students').orderBy('name').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'student_id': doc.data().student_id,
                    'name': doc.data().name,
                    'gender': doc.data().gender,
                    'grade': doc.data().grade,
                    'section': doc.data().section,
                    'birthday': doc.data().birthday
                }
                this.students.push(data)
            })
        })
    },
    methods: {
        logout () {
            firebase.auth().signOut().then(() => {
                this.$router.replace('login')
            })
        }
    }
}
</script>


