<template>
    <div id="edit-student">
        <h3>Edit Employee</h3>
        <div class="row">
            <form @submit.prevent="updateStudent" class="col s12">
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Student ID Number</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="student_id" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Student Name</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Birthday</p>
                    <div class="input-field col s12">
                        <input type="date" v-model="birthday" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Gender</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="gender" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Grade</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="grade" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Section</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="section" required>
                    </div>
                </div>
                <div class="view-options">
                    <button type="submit" class="btn btn-large bg-icon">Submit</button>
                    <router-link to="/s" class="btn btn-large grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'edit-student',
    data () {
        return {
            student_id: null,
            name: null,
            grade: null,
            gender: null,
            section: null,
            birthday: null
        }
    }, 
    // navigation guard : before page loads
    beforeRouteEnter(to, from, next) {
        db.collection('students').where('student_id', '==', to.params.student_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.student_id = doc.data().student_id
                    vm.name = doc.data().name
                    vm.grade = doc.data().grade
                    vm.gender = doc.data().gender
                    vm.grade = doc.data().grade
                    vm.birthday = doc.data().birthday
                    vm.section = doc.data().section
                })
            })
        })
    },
    //watcher
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('students').where('student_id', '==', this.$route.params.student_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach( doc => {
                    this.student_id = doc.data().student_id
                    this.name = doc.data().name
                    this.grade = doc.data().grade
                    this.gender = doc.data().gender
                    this.section = doc.data().section
                    this.birthday = doc.data().birthday
                })
            })
        },
        updateStudent() {
            db.collection('students').where('student_id', '==', this.$route.params.student_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach( doc => {
                    doc.ref.update({
                        student_id: this.student_id,
                        name: this.name,
                        grade: this.grade,
                        gender: this.gender,
                        section: this.section,
                        birthday: this.birthday
                    }).then(() => {
                        this.$router.push({name: 'view-student', params: {student_id: this.student_id}})
                    })
                })
            })
        }
    }
}
</script>


